@import "../../variables/variables.scss";
.skills__container {
  position: absolute;
  top: 23.5%;
  left: 27.3%;
  width: 47.6%;
  height: 64.2%;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(5, 83, 121, 1) 18%,
    rgba(255, 255, 255, 1) 100%
  );
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  .skills__title {
    text-align: center;
    color: rgb(14, 3, 75);
    padding: 40px 0 20px;
    font-size: 40px;

    font-family: $fontB;
    text-transform: uppercase;
  }

  .skills__items {
    padding-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 3%;
    margin: 0 auto;
    .skills__item {
      width: 100px;
      height: 100px;
      // margin-bottom: 5%;
      color: white;
      border-radius: 25px;
      &-html {
        background-color: #ff6d00;
        border: 1.5px solid #e65100;
      }
      &-css {
        background-color: #039be5;
        border: 1.5px solid #0277bd;
      }
      &-js {
        background-color: #f2bf22;
        border: 1.5px solid #e7a42b;
      }
      &-react {
        background-color: #8bb7f0;
        border: 1.5px solid #5078b7;
      }
      &-redux {
        background-color: #7e57c2;
        border: 1.5px solid #5f4094;
      }
      &-eslint {
        background-color: #7986cb;
        border: 1.5px solid #3949ab;
      }
      &-git {
        background-color: #f4511e;
        border: 1.5px solid #be350c;
      }
      &-sass {
        background-color: #f06292;
        border: 1.5px solid #ec6494;
      }
      &-vsc {
        background-color: #29b6f6;
        border: 1.5px solid #0288d1;
      }
      &-rwd {
        background-color: #1ed7eb;
        border: 1.5px solid #1e8ceb;

        .skills__item-image {
          display: flex;
          justify-content: center;
          align-items: center;
          img {
            width: 90%;
            height: 90%;
          }
        }
      }
      &-image {
        width: 100%;
        height: 80%;
        background-color: white;
        border-bottom-left-radius: 25px;
        border-bottom-right-radius: 25px;
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;
        // border: 1px solid gray;
        img {
          height: 100%;
          width: 100%;
        }
      }
      &-name {
        text-align: center;
      }
    }
  }
  .skills__projects-navigator {
    text-align: center;
    font-family: $fontD;
    font-size: 20px;
    color: white;
    span {
      color: rgb(127, 230, 127);
      text-decoration: underline;
      cursor: pointer;
    }
  }
}
